import React from 'react'
import classNames from 'classnames'
import { page, linguage1, linguage2 } from 'data/pages/03.json'
import Button from 'components/Common/Button'
import ComponentList from 'components/Common/ComponentList'
import H2 from 'components/HTML/H2'
import H3 from 'components/HTML/H3'
import HTML from 'components/HTML/HTML'
import P from 'components/HTML/P'
import PageView from 'components/Views/PageView'
import ScrollTo from 'components/Common/ScrollTo'
import Section from  'components/HTML/Section'
import Slide from 'components/Slider/Slide'
import Slider from 'components/Slider/Slider'

export default () => (
  <PageView {...page}>
    <Section1 />
    <Section2 />
    <SectionLinguage {...linguage1} className="BGLightGray" />
    <SectionLinguage {...linguage2} className="BGWhite" />
  </PageView>
)

const Section1 = () => (
  <Slider id="target">
    <Slide>
      <div style={{ height: 320 }}>
        <H3>Nós falamos o português correto sempre</H3>
      </div>
    </Slide>
  </Slider>
)

const Section2 = () => (
  <Section className="BGSecondary">
    <H2>
      Título lorem ipsum sobre linguagem ativa e passiva
    </H2>
    <P>
      Nós praticamos 2 tipos de linguagem, a ATIVA e a REATIVA.
      Elas permitem que nós sejamos acolhedores sem desrespeitar as nossas regras de linguagem.
    </P>
    <ScrollTo to="linguagem" offsetTop={80}>
      <Button className="BtnOutlineInverted">Vamos lá</Button>
    </ScrollTo>
  </Section>
)


const SectionLinguage = ({ slide1, slide2, title, className }) => (
  <section id="linguagem" className={classNames(className, 'PT3x PH2x')}>
    <div className="TextCenter">
      <H3 className="Medium">{title}</H3>
    </div>
    <Slider fix>
      <Slide1 {...slide1} />
      <Slide2 {...slide2} />
    </Slider>
  </section>
)

const Slide1 = ({ list }) => (
  <Slide>
    <ComponentList component={Step}>
      {parseList(list)}
    </ComponentList>
  </Slide>
)

const parseList = list => list.map(text => ({ text }))

const Step = ({ index, text }) => (
  <div className="Flex JCCenter PB16 TextLeft">
    <div className="Flex AICenter W100" style={{ maxWidth: 400 }}>
      <div className="ColorPrimary Saira SairaLight FS9x FlexGrow">
        {index + 1}.
      </div>
      <HTML className="W80 SingleSpace ColorGray">
        {text}
      </HTML>
    </div>
  </div>
)

const Slide2 = ({ text, listTitle, list }) => (
  <Slide>
    <P>{text}</P>
    <p className="Saira Light ColorPrimary FS4x">{listTitle}</p>
    <ComponentList component={Li}>
      {parseList(list)}
    </ComponentList>
  </Slide>
)

const Li = ({ text }) => (
  <div className="Flex TextLeft MHAuto" style={{ maxWidth: 420 }}>
    <div className="PH16 ColorPrimary">
      <svg style="width:24px;height:24px" viewBox="0 0 24 24">
          <path fill="#000000" d="M12,10A2,2 0 0,0 10,12C10,13.11 10.9,14 12,14C13.11,14 14,13.11 14,12A2,2 0 0,0 12,10Z" />
      </svg>
    </div>
    <p className="ColorBlack SingleSpace Opacity5 FS2x PB8 M0">{text}</p>
  </div>
)
